import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDownload } from "@fortawesome/free-solid-svg-icons";
import "./DownloadApp.css";
const DownloadApp = () => {
  const [browserSize, setbrowserSize] = useState(window.innerWidth);
  const [isLoading, setisLoading] = useState(false);
  const [thanksmessage, setthanksmessage] = useState("");
  const download = () => {
    const link =
      "https://docs.google.com/u/0/uc?export=download&confirm=h784&id=1NvEyARJXzHIFF4uxA4kTWlh0NfWV-lUd";
    setisLoading(true);
    fetch("https://naijadaily.000webhostapp.com/send mail.php", {
      method: "POST",
      headers: {
        Accept: "application/json",
      },
    })
      .then((res) => res.json())
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
    setisLoading(false);
    window.open(link, "_blank");
    setthanksmessage("Thank You For Downloading Our Mobile App!");
  };

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  if (isLoading) {
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100vh",
        }}
      >
        <img
          width="100px"
          src={require("../../assets/Chunk-4s-200px.gif")}
          alt=""
        />
      </div>
    );
  }

  return (
    <div>
      <div className="download">
        <h1 className="downloadmobile">
          Download The Naija Daily Mobile App Below
        </h1>
        <div className="download-image">
          <img
            className="image-download"
            src={require("../../assets/hompage.png")}
            alt=""
          />
          <img
            className="image-download"
            src={require("../../assets/detailspage.png")}
            alt=""
          />

          <img
            className="image-download"
            src={require("../../assets/sidemenu.png")}
            alt=""
          />
        </div>

        <div className="download-image">
          <img
            className="image-download"
            src={require("../../assets/share.png")}
            alt=""
          />
          <img
            className="image-download"
            src={require("../../assets/bottom.png")}
            alt=""
          />

          <img
            className="image-download"
            src={require("../../assets/metro.png")}
            alt=""
          />
        </div>

        {/* <h1 className="downloadtxt2">Download Mobile App Below</h1> */}
        <h1 className="thanks-message">{thanksmessage}</h1>
        <button onClick={download} className="download-btn">
          {" "}
          <FontAwesomeIcon color="#fff" icon={faDownload} /> Download App
        </button>
      </div>
    </div>
  );
};

export default DownloadApp;
